import React from 'react';
import { get } from 'lodash';
import { DisplayColumn } from '@jutro/datatable';
import { Link } from '@jutro/components';
import { FileImage } from '@business-patterns/components';
import styles from './ThumbnailColumn.module.scss';

export const ThumbnailColumn = () => {
    if (__DEV__) {
        throw new Error('Component <ThumbnailColumn /> should never render');
    }
    return <React.Fragment />;
};

ThumbnailColumn.defaultCell = (row, rowId, { path, visible, onClick }) => {
    const file = get(row, path);

    if (visible === false) {
        return null;
    }

    if (!file) {
        return DisplayColumn.defaultCell(row, rowId, { path });
    }

    return (
        <div className={styles.thumbnailWrapper}>
            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
            <Link
                onClick={() => onClick(row, rowId, { path })}
                title="Show image"
            >
                <FileImage
                    value={file}
                    className={styles.thumbnail}
                    hideLoader
                />
            </Link>
        </div>
    );
};

ThumbnailColumn.displayName = 'ThumbnailColumn';

ThumbnailColumn.defaultProps = {
    ...DisplayColumn.defaultProps,
    renderCell: ThumbnailColumn.defaultCell,
};
