// @ts - check
import React, { useEffect, useMemo, useState } from 'react';
import { Image } from '@jutro/components';
import { omit } from 'lodash/fp';

const useImageAsDataUrl = file => {
    const [dataUrl, setDataUrl] = useState();
    const reader = useMemo(() => new FileReader(), []);

    useEffect(() => {
        if (!file || !(file instanceof Blob)) {
            setDataUrl(undefined);
            return;
        }
        reader.onload = () => setDataUrl(reader.result);
        reader.readAsDataURL(file);
    }, [file, reader]);

    return dataUrl;
};

const omitMetadataProps = omit([
    'showRequired',
    'showOptional',
    'showErrors',
    'onValidationChange',
    'onValueChange',
]);

export const FileImage = ({ value, hideLoader = false, ...rest }) => {
    const imageProps = omitMetadataProps(rest);
    const dataUrl = useImageAsDataUrl(value);

    if (!value || (value && !dataUrl && hideLoader)) {
        return null;
    }
    return <Image {...imageProps} src={dataUrl} />;
};
