import React, { useContext } from 'react';
import { TranslatorContext } from '@jutro/locale';
import { Button } from '@jutro/components';

import styles from './Card.module.scss';

const getStyle = suffix => styles[`business-card${suffix ? `-${suffix}` : ''}`];

export const Card = ({ maxWidth, children, title, id, headerAction }) => {
    const translator = useContext(TranslatorContext);
    return (
        <div id={id} className={getStyle()} style={{ maxWidth }}>
            {title && (
                <React.Fragment>
                    <h2 className={getStyle('title')}>{translator(title)}</h2>
                    {headerAction && (
                        <Button
                            {...headerAction}
                            className={getStyle('action')}
                        >
                            {translator(headerAction.label)}
                        </Button>
                    )}
                    <hr className={getStyle('line')} />
                </React.Fragment>
            )}
            {children}
        </div>
    );
};
Card.displayName = 'Card';
Card.defaultProps = {
    maxWidth: 580,
};
